function mostrarSeccion(){

    //Click en seccion uno
    const seccion = document.querySelectorAll('.seleccion');
    
    const cero = document.querySelector('.wallpaper0');
    cero.classList.remove('bloquear');
    const uno = document.querySelector('.wallpaper1');
    uno.classList.add('bloquear');
    const dos = document.querySelector('.wallpaper2');
    dos.classList.add('bloquear');
    const tres = document.querySelector('.wallpaper3');
    tres.classList.add('bloquear');

    const removerSeleccion = document.querySelector('.seleccion2');
    cero.classList.add('seleccion2');

    const idCero = document.querySelector('#cero');
    const idUno = document.querySelector('#uno');
    const idDos = document.querySelector('#dos');
    const idTres = document.querySelector('#tres');

    seccion.forEach((seccion)=>{

        seccion.addEventListener('click', (e) =>{
            
            if (e.target.dataset.id == "0"){
                const removerSeleccion = document.querySelector('.seleccion2');
                removerSeleccion.classList.remove('seleccion2');
                idCero.classList.add('seleccion2')
                cero.classList.remove('bloquear');
                uno.classList.add('bloquear');
                dos.classList.add('bloquear');
                tres.classList.add('bloquear');
            }
            else if(e.target.dataset.id == "1"){
                const removerSeleccion = document.querySelector('.seleccion2');
                removerSeleccion.classList.remove('seleccion2');
                idUno.classList.add('seleccion2')
                cero.classList.add('bloquear');
                uno.classList.remove('bloquear');
                dos.classList.add('bloquear');
                tres.classList.add('bloquear');
                
            } 
            
            else if(e.target.dataset.id == "2"){
                const removerSeleccion = document.querySelector('.seleccion2');
                removerSeleccion.classList.remove('seleccion2');
                idDos.classList.add('seleccion2')
                cero.classList.add('bloquear');
                uno.classList.add('bloquear');
                dos.classList.remove('bloquear');
                tres.classList.add('bloquear');
               
            } else if (e.target.dataset.id == "3"){
                const removerSeleccion = document.querySelector('.seleccion2');
                removerSeleccion.classList.remove('seleccion2');
                idTres.classList.add('seleccion2')
                cero.classList.add('bloquear');
                uno.classList.add('bloquear');
                dos.classList.add('bloquear');
                tres.classList.remove('bloquear');
            }
        })
    })     
}

function desplegarMenu(){

    const menu = document.querySelector('.fa-bars');
    menu.addEventListener('click', e =>{
        const lista = document.querySelector('.lista');
        const desbloquear = document.querySelector('.desbloquear');
        if(desbloquear){
            lista.classList.remove('desbloquear');
    
        } else{
            lista.classList.add('desbloquear');
            lista.classList.add('act');
        }
        desdesplegarMenu();
    })

};

function desdesplegarMenu(){
    const div = document.querySelector('.wallpaper0');

    div.addEventListener('click', e=>{
        const listas = document.querySelector('.lista');
        const act = document.querySelector('.act');
        if(act){
            listas.classList.remove('desbloquear');
        } else{
            listas.classList.add('desbloquear');
        }
    })
}

function agrandarImagen(){
    const body = document.querySelector('.body');
    const imagenSel = document.querySelectorAll('img');

    imagenSel.forEach(elemento=>{
        const imagenSel2 = document.querySelectorAll('.img');
        elemento.addEventListener('click', e=>{

            
            body.classList.add('grande')
        })
        
    })
}

